import "./Banner.css";
import axios from "axios";

function Banner() {
  return (
    <div className="Banner">
      <div className="main">
        <h1 className="name">Bheeshma Parvam</h1>
        <div className="buttons">
          <button
            className="button"
            onClick={() =>
              axios
                .get("https://jsonplaceholder.typicode.com/posts")
                .then((response) => {
                  console.log(response.data);
                })
            }
          >
            Play
          </button>
          <button className="button">My list</button>
        </div>
        <h4 className="content">
          "Bheeshma Parvam" tells the story of Michael, a former gangster turned
          marine exporter from Fort Kochi. Ensuing a series of unfortunate
          events and death threats to his family, he is forced to revisit his
          past.
        </h4>
        <br />
        <br />
      </div>
      <div className="fade"></div>
    </div>
  );
}
export default Banner;
